<template>
  <div class="loading-latout position-fixed top-0 start-0" v-if="isLoading">
    <semipolar-spinner
      :animation-duration="2000"
      :size="80"
      color="#b41f23"
      class="loading"
    />
  </div>
  <div class="layout-main">
    <div class="page">
      <div class="page-head">
        <div class="container-fluid">
          <h1 class="page-title">KYC Policy of Value Entertainment N.V.,</h1>
          <div class="page-date">Last updated: April 13, 2023</div>
        </div>
      </div>
      <div class="container-fluid">
        <div class="content">
          <section>
            <p>When a user makes an aggregate lifetime total of deposits exceeding EUR 2000 or requests a withdrawal
              of any amount inside the <a href="https://casinotheworld.com/" target="_blank">https://casinotheworld.com/</a> Platform, then it is compulsory for them to perform a
              full KYC process.
            </p>
            <p>During this process, the user will have to input some basic details about themselves and then upload</p>
            <ol>
              <li>
                <p>A copy of Government Issued Photo ID (in some cases front and back depending on the doc)</p>
              </li>
              <li>
                <p>A selfie of themselves holding the ID doc</p>
              </li>
              <li>
                <p>A bank statement/Utility Bill</p>
              </li>
            </ol>
            <p>Once uploaded, the user will get a “Temporarily Approved” Status and the documents will now be on our
              side, and the “KYC Team” will have 24hrs to go over them and email the user about the outcome:
            </p>
            <ul style="list-style: square;">
              <li>
                <p>Approval</p>
              </li>
              <li>
                <p>Rejection</p>
              </li>
              <li>
                <p>More information needed – No change in Status</p>
              </li>
            </ul>
            <p>When the user is on “Temporarily Approved” Status then</p>
            <ul style="list-style: square;">
              <li>
                <p>They can use the platform normally</p>
              </li>
              <li>
                <p>They cannot deposit more than EUR 500 in aggregate total</p>
              </li>
              <li>
                <p>They cannot complete any withdrawal.</p>
              </li>
            </ul>
            <h5>Guideline for the “KYC Process”</h5>
            <ol>
              <li style="margin-bottom: 8px;">
                Proof of ID
                <ul style="list-style-type: lower-alpha; margin-top: 8px;">
                  <li>Signature Is there</li>
                  <li>Country is not a Restricted Country: United States of America and its territories,
                    France and its territories, Netherlands and its territories and countries that form the
                    Kingdom of Netherlands including Bonaire, Sint Eustatius, Saba, Aruba, Curacao and
                    Sint Maarten, Australia and its territories, United Kingdom of Great Britain, Northern
                    Ireland, Spain.
                  </li>
                  <li>Full Name matches client’s name</li>
                  <li>Document does not expire in the next 3 months</li>
                  <li>Owner is over 18 years of age</li>
                </ul>
              </li>
              <li style="margin-bottom: 8px;">
                Proof of Residence
                <ul style="list-style-type: lower-alpha; margin-top: 8px;">
                  <li>Bank Statement or Utility Bill</li>
                  <li>Country is not a Restricted Country: United States of America and its territories,
                    France and its territories, Netherlands and its territories and countries that form the
                    Kingdom of Netherlands including Bonaire, Sint Eustatius, Saba, Aruba, Curacao and
                    Sint Maarten, Australia and its territories, United Kingdom of Great Britain, Northern
                    Ireland, Austria, Germany, Spain, and Cyprus.
                  </li>
                  <li>Full Name matches client’s name and is same as in proof of ID.</li>
                  <li>Date of Issue: In the last 3 months</li>
                </ul>
              </li>
              <li>
                Selfie with ID
                <ul style="list-style-type: lower-alpha; margin-top: 8px;">
                  <li>Holder is the same as in the ID document above</li>
                  <li>ID document is the same as in “1”. Make sure photo/ID number is the same</li>
                </ul>
              </li>
            </ol>
            <h5>Notes on the “KYC Process”</h5>
            <ol>
              <li>
                <p>When the KYC process is unsuccessful then the reason is documented and a support ticket is
                  created in the system. The ticket number along with an explanation is communicated back to the
                  user.
                </p>
              </li>
              <li>
                <p>Once all proper documents are in our possession then the account gets approved.</p>
              </li>
            </ol>
            <h5>“Other AML measures”</h5>
            <ol>
              <li>
                <p>If a user has not passed full KYC then they cannot make additional deposits or withdrawals of
                  any amount.</p>
              </li>
              <li>
                If a user has passed the KYC process successfully then
                <ul style="list-style-type: lower-alpha; margin-top: 8px;">
                  <li>There is a deposit limit per transaction (max EUR 2,000)</li>
                  <li>Prior to any withdrawal there is a detailed algorithmic and manual check on the activity
                    and balance of the user to see if the amount withdrawn is a result of proper activity in the
                    platform.
                  </li>
                </ul>
              </li>
              <li style="margin-top: 16px;">Under no circumstances may a user transfer funds directly to another user.</li>
            </ol>
          </section>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import loadingMixin from '@/mixins/loadingMixin'
export default {
  mixins: [loadingMixin]
}
</script>
